
import listItem from '@/components/listItem'
import ShopBtn from '@/components/shopBtn'
import Cell from 'vux/src/components/cell/index.vue'
import Group from 'vux/src/components/group/index.vue'
import FormPreview from 'vux/src/components/form-preview/index.vue'
import XTextarea from 'vux/src/components/x-textarea/index.vue'
import Popup from 'vux/src/components/popup/index.vue'
import Checklist from 'vux/src/components/checklist/index.vue'
import Checker from 'vux/src/components/checker/checker.vue'
import CheckerItem from 'vux/src/components/checker/checker-item.vue'
import XNumber from 'vux/src/components/x-number/index.vue'

import { submitOrderRefund, getOrderDetail, getOrderRefundDetail } from '@/api/user'
import { uploadFile, uploadWechatFile } from '@/api/other.js'
export default {
name: 'uc-submitRefunds',
components: {
listItem,
Group,
Cell,
FormPreview,
XTextarea,
ShopBtn,
Popup,
Checklist,
CheckerItem,
XNumber,
Checker
},
data () {
return {
localIds: [], // 微信图片id
serverId: [],
productData: [],
picArrList: [],
orderDetail: {},
orderRefundDetail: {},
show: false,
show2: false,
picArr: [],
status: 0, // 货物状态 0未收到货物 1已收到
checkList: [],
commonList: [ '7天无理由', '退运费', '商品不符', '商品瑕疵', '其他' ],
reason: [],
txt: '',
checklist002: [ 'China', 'Japan' ],
postData: {
pic: ''
}
}
},
computed: {
totalMoney () {
let total = 0
this.checkList.map(v => {
console.log("aa"+v)
this.orderRefundDetail.productinfo.map((item, index) => {
if (v === index) {
total += item.num * item.refundprice
}
})
})
return total
},
refundGoods () {
let list = ''
this.checkList.map(v => {
console.log(v)
this.orderRefundDetail.productinfo.map((item, index) => {
if (v === index && list === '') {
list = item.skuid + ':' + item.num
} else if (v === index) {
list = list + ',' + item.skuid + ':' + item.num
}
})
})
return list
}
},
created () {
const postData = {
orderid: this.$route.query.id
}

getOrderDetail(postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

res.data.data.productinfo.map(item => {
item.max = item.num
})
this.orderDetail = res.data.data
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})

this.getOrderRefundDetail()
},
methods: {

getOrderRefundDetail(){
this.$vux.loading.show()
const postData = {
orderid: this.$route.query.id
}
getOrderRefundDetail(postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {


res.data.data.productinfo.map((item,index )=> {
item.max = item.num
this.checkList.push(index)
})
this.orderRefundDetail = res.data.data
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},
chooseImg () {
console.log(this.$wechat)
const that = this
this.$wechat.chooseImage({
count: 3 - that.picArr.length, // 默认9

sizeType: ['original', 'compressed'], // 可以指定是原图还是压缩图，默认二者都有
sourceType: ['album', 'camera'], // 可以指定来源是相册还是相机，默认二者都有
success: function (res) {
that.picArr = that.picArr.concat(res.localIds)
that.localIds = that.picArr
that.picArrList = that.picArr
console.log(JSON.stringify(res.localIds))
}
})
},

syncUpload () {
if (!this.localIds.length) {


this.syncSubmit()
} else {
const that = this
const localId = this.localIds.pop()
that.$wechat.uploadImage({
localId: localId,
isShowProgressTips: 0,
success: function (res) {
that.serverId.push(res.serverId)
that.syncUpload()
}
})
}
},

syncSubmit () {

if (!this.serverId.length) {


const postData = {
orderid: this.orderRefundDetail.orderid,
skuid: this.refundGoods,
status: 0,
reason: this.reason[0],
txt: this.txt,
pic: this.postData.pic
}

if (this.orderDetail.orderstatus === 'recieve') {
postData.status = this.status
}
this.$vux.loading.show()
submitOrderRefund(postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('申请成功', 'middle')
this.$router.replace({
name: 'uc-refundDetail',
query: {
id: this.orderRefundDetail.orderid
}
})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
} else {
const that = this
const serverId = this.serverId.pop()
that.handleUpload(serverId)
}
},

handleUpload (val) {
const postData = {
mediaid: val
}
uploadWechatFile(postData)
.then(res => {


if (res.data.errorcode === 0) {



this.postData.pic = this.postData.pic ? this.postData.pic + ',' + res.data.data.filePath : res.data.data.filePath
this.syncSubmit()

} else {

this.$vux.loading.hide()

this.$vux.toast.text('图片上传失败', 'middle')
return false
}
})
.catch(err => {

console.log(err)
this.$vux.loading.hide()

this.$vux.toast.text('图片上传失败', 'middle')
return false
})
},
change (val, label) {
this.show2 = false
console.log('change', val, label)
},
handleSubmit () {







if (this.checkList.length < 1) {
this.$vux.toast.text('请选择退款商品！')
return
}
if (!this.reason[0]) {
this.$vux.toast.text('请选择退款原因！')
return
}
this.$vux.loading.show()
if (this.picArr.length > 0) {


const that = this
this.postData.pic = ''
that.syncUpload()
} else {
const postData = {
orderid: this.orderRefundDetail.orderid,
skuid: this.refundGoods,
status: this.status,
reason: this.reason[0],
txt: this.txt

}




this.$vux.loading.show()
submitOrderRefund(postData)
.then(res => {
this.$vux.loading.hide()
if (res.data.errorcode === 0) {

this.$vux.toast.text('申请成功', 'middle')
this.$router.replace({
name: 'uc-refundDetail',
query: {
id: this.orderRefundDetail.orderid
}
})
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
this.$vux.loading.hide()
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
}
},

fileChange (obj) {
const file = obj.target.files[0]
if (file.type.toUpperCase() === 'IMAGE/PNG' || file.type.toUpperCase() === 'IMAGE/JPEG') {

} else {
this.$vux.toast.text('只支持PNG/JPEG格式的图片')
return
}

if ((file.size / 1024) > 3072) {
this.$vux.toast.text('最大只支持3M的图片')
return
}

if (this.picArr.length > 3) {
this.$vux.toast.text('最多3张图片')
return
}
if (this.picArr.length === 2) {
this.$vux.toast.text('最多上传3张图片')
}

console.log('开始上传图片')

let param = new FormData() 
param.append('file', file, file.name) 
console.log(param.get('file'))
uploadFile(param)
.then(res => {
if (res.data.errorcode === 0) {


this.picArr.push(res.data.data.filePath)
this.$vux.toast.text('上传成功', 'middle')
} else {
this.$vux.toast.text(res.data.errormsg, 'middle')
}
})
.catch(err => {
console.log(err)
this.$vux.toast.text('网络异常！', 'middle')
})
},

handleDelImg (index) {
this.picArr.splice(index, 1)
this.localIds = this.picArr
console.log(this.picArr)
}
}
}



